  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
}

.mobile ul li a {
  color: #fff;
  display: block;
  padding: 1em 0;
}

@media only screen and (min-width: 1024px) {
  .mobile.open.left {
    transform: translateX(-100%);
  }
  .mobile.open.right {
    transform: translateX(100%);
  }
}

