.features > * p {
  margin-bottom: 1rem;
}

.features > * .button {
  margin-bottom: 0;
}

@media only screen and (min-width: 1024px) {
  .features {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
  }
  .features > * {
    display: flex;
    flex: 1 0 0;
    flex-direction: column;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
  }
  .features > * .button {
    align-self: center;
    margin-top: auto;
  }
}

header.primary {
  position: fixed;
  top: 0;
  transition: top 0.2s ease-in-out;
  width: 100%;
  z-index: 100;
}

.tabs {
  position: relative;
}

.tabs nav {
  border-bottom: 1px solid;
}

.tabs nav ul {
  list-style: none;
  margin-left: 20px;
  padding: 0;
  text-align: center;
}

.tabs nav li {
  background-color: #CBBFB4;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  cursor: pointer;
  display: inline-block;
  margin-bottom: 0;
  margin-right: 0.5em;
  padding: 0.5em 1em;
}

.tabs nav li.active {
  background-color: #FFF;
  border: 1px solid;
  border-bottom: none;
  position: relative;
}

.tabs nav li.active::after {
  background-color: #FFF;
  bottom: -1px;
  content: "";
  height: 1px;
  left: 0;
  position: absolute;
  width: 100%;
}

.tabs .panel {
  display: none;
  padding: 20px;
}

.tabs .panel.active {
  display: inherit;
}

@media only screen and (min-width: 1024px) {
  .tabs nav ul {
    margin-left: 20px;
    text-align: left;
  }
}

/* = Media Queries = */
@media only screen and (min-width: 669px) {
  /* = Layout = */
  aside.primary {
    float: right;
    margin-bottom: 0;
    width: 20%;
  }
  aside.primary nav ul {
    text-align: left;
  }
  aside.primary ~ main {
    float: left;
    padding-right: 2rem;
    width: 80%;
  }
}

@media only screen and (min-width: 1024px) {
  /* = Layout = */
  .wrapper {
    overflow: auto;
    padding-top: 102px;
  }
  nav ul {
    text-align: left;
  }
  nav.primary {
    margin-bottom: 0;
  }
  nav.primary > ul > li {
    font-size: 1.3rem;
    margin-left: 1rem;
    position: relative;
  }
  nav.primary > ul > li:first-child {
    margin-left: 0;
  }
  nav.primary ul {
    text-align: left;
  }
  nav.primary ul li li {
    display: block;
    float: none;
  }
  nav.primary ul .dropdown {
    background-color: #191919;
    left: 0;
    min-width: 180px;
    position: absolute;
    z-index: 2;
  }
  nav.primary ul li:hover .dropdown {
    display: block;
  }
  nav.primary ul .dropdown li {
    font-size: 0.9rem;
  }
  nav.primary ul .dropdown li a {
    color: #FFF;
    padding: 0.3rem 0.8rem;
  }
  nav.primary ul .dropdown li a:hover {
    background-color: #333333;
  }
  .utilities nav.utility {
    margin-bottom: 0.4rem;
  }
  .utilities nav.utility ul {
    text-align: right;
  }
  .desktop {
    display: flex;
  }
  .menu-toggle {
    display: none;
  }
  aside.primary {
    width: 25%;
  }
  aside.primary ~ main {
    width: 75%;
  }
  /* = Typography = */
  h1 {
    font-size: 2.6rem;
  }
  h2 {
    font-size: 2.1rem;
  }
  h3 {
    font-size: 1.6rem;
  }
  h4 {
    font-size: 1.25rem;
  }
  h5 {
    font-size: 1.15rem;
  }
}

/* = Print Styles = */
@media print {
  * {
    background-color: transparent !important;
    box-shadow: none !important;
    color: #000 !important;
    /* Black prints faster: h5bp.com/s */
    text-shadow: none !important;
  }
  a {
    text-decoration: underline;
  }
  pre,
  blockquote,
  tr,
  img,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    page-break-inside: avoid;
  }
  thead {
    display: table-header-group;
  }
  @page {
    margin: 0.5cm;
  }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }
}
